p{
    line-height:1;
}

.headerLink{
    font-weight:bold;
}
.scoreLine{
    padding-right:0px !important;
}

.scoreHeader{
  font-size:1rem;
}

.note {
    margin-top:4px;
    margin-bottom:4px;
  }
  
  .note > p {
    font-weight:normal;
  }

.dateTime {
    font-weight:normal;
    font-style:italic;
}

.reviewCard {
    box-shadow: 0 2px 0 rgba(90,97,105,.11), 0 4px 8px rgba(90,97,105,.12), 0 10px 10px rgba(90,97,105,.06), 0 7px 70px rgba(90,97,105,.1);
    padding: 19px;
    margin:15px;
    border-radius: .25rem;
    background-color:white;
}

.borderTop {
    border-top:1px solid #e1e5eb;
    padding-top:4px;
    margin-top:4px;
}

.comments {
    border-top:1px solid #e1e5eb;
}

.addCommentButton {
    margin-top:10px;
}

